<template>
    <div><a id="jumpUrl" href="uniondrugshop://app/liveapp?name=education"></a></div>
</template>

<script>
    export default {
        name: "link",
        created() {
            let u = navigator.userAgent;
            let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
            if (isiOS) {
                window.location.href = 'uniondrugshop://app/liveapp?name=education'
            } else {
                console.log(1)
                this.$dialog.confirm({
                    title: "",
                    message: "在“药联药店宝”中打开此页？",
                })
                    .then(() => {
                        var link = document.getElementById("jumpUrl");
                        link.click();
                        var preventDefault = function (e) {
                            e = e || window.event;
                            if (e.preventDefault) {
                                e.preventDefault();
                            } else {
                                e.returnValue = false;
                            }
                        };
                        preventDefault();
                    })
                    .catch(() => {});
            }
        }
    }
</script>

